html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.slideout-menu {
  position: fixed;
  z-index: 8;
  top: 0;
  bottom: 0;
  display: none;
  overflow-y: scroll;
  width: 256px;
  min-height: 100vh;
  -webkit-overflow-scrolling: touch;
  background: #181a22;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}

.slideout-menu::-webkit-scrollbar {
  width: 0;
}

.slideout-menu-right {
  right: 0;
  overflow-x: hidden;
}

.slideout-menu-right ul {
  list-style: none;
  padding-left: 10px;
  padding-top: 35px;
  position: relative;
}

.slideout-menu-right li {
  padding-top: 35px;
  padding-left: 40px;
}

.slideout-menu-right a {
  color: #cdcdcd;
  font-size: 20px;
  font-weight: 100;
  transition: all 0.3s;
}

.slideout-menu-right .user ul {
  padding-top: 5px;
}

.slideout-menu-left {
  left: 0;
}

.slideout-panel {
  position: relative;
  z-index: 9;
  min-height: 100vh;
  background-color: #fff;
  will-change: transform;
}

.slideout-open {
  overflow: hidden;
}

.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden;
}

.slideout-open .slideout-menu {
  display: block;
}

.panel:before {
  content: '';
  display: block;
  background-color: transparent;
  transition: background-color 0.5s ease-in-out;
}

.panel-open:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}

#btn {
  display: block;
  width: 36px;
  height: 20px;
  padding-top: 20px;
  cursor: pointer;
}

#btn span {
  position: relative;
  display: block;
  width: 36px;
  height: 5px;
  background-color: #606060;
  transition: all 0.3s;
  backface-visibility: hidden;
}

#btn span:after, #btn span:before {
  content: "";
  position: absolute;
  left: 0;
  top: -10px;
}

#btn span:after {
  top: 10px;
  width: 36px;
  height: 5px;
  background-color: #606060;
  transition: all 0.3s;
  backface-visibility: hidden;
}

#btn span:before {
  width: 36px;
  height: 5px;
  background-color: #606060;
  transition: all 0.3s;
  backface-visibility: hidden;
}

#btn.on span {
  background-color: transparent;
}

#btn.on span:before {
  transform: rotate(45deg) translate(6px, 5px);
}

#btn.on span:after {
  transform: rotate(-45deg) translate(9px, -8px);
}

#btn.on {
  display: none;
}

.menu-close {
  position: absolute;
  top: 20px;
  right: 10%;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.menu-close span {
  background-color: transparent;
  position: relative;
  top: 20px;
}

.menu-close span:after, .menu-close span:before {
  content: "";
  position: absolute;
  left: 0;
  top: -10px;
}

.menu-close span:after {
  content: '';
  top: 10px;
  width: 36px;
  height: 5px;
  background-color: #606060;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
  transform: rotate(-45deg) translate(9px, -8px);
}

.menu-close span:before {
  content: '';
  transform: rotate(45deg) translate(6px, 5px);
  width: 36px;
  height: 5px;
  background-color: #606060;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
}

.top-line-anchor {
  display: inline-block;
}

@media (max-width: 767px) {
  .top-line-anchor {
    left: 20px;
  }
}

.top-line-anchor {
  display: none;
  position: absolute;
  left: 10px;
  top: 10px;
}

@media (max-width: 767px) {
  .top-line-anchor {
    display: block;
  }
}

@media (max-width: 767px) {
  .header nav {
    display: none;
  }
}

.block {
  display: flex;
}
